<h1 id="服务商相关接口" tabindex="-1"><a class="header-anchor" href="#服务商相关接口" aria-hidden="true">#</a> 服务商相关接口</h1>
<h2 id="单点登录" tabindex="-1"><a class="header-anchor" href="#单点登录" aria-hidden="true">#</a> 单点登录</h2>
<h3 id="获取从第三方单点登录连接" tabindex="-1"><a class="header-anchor" href="#获取从第三方单点登录连接" aria-hidden="true">#</a> 获取从第三方单点登录连接</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">provider</span><span class="token operator">-></span><span class="token function">getLoginUrl</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$redirectUri</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$userType</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'admin'</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$state</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">//$redirectUri 回调地址  $userType支持登录的类型</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="获取登录用户信息" tabindex="-1"><a class="header-anchor" href="#获取登录用户信息" aria-hidden="true">#</a> 获取登录用户信息</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">provider</span><span class="token operator">-></span><span class="token function">getLoginInfo</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$authCode</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">//$authCode oauth2.0授权企业微信管理员登录产生的code，最长为512字节。只能使用一次，5分钟未被使用自动过期</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="注册定制化" tabindex="-1"><a class="header-anchor" href="#注册定制化" aria-hidden="true">#</a> 注册定制化</h2>
<h3 id="获取注册码" tabindex="-1"><a class="header-anchor" href="#获取注册码" aria-hidden="true">#</a> 获取注册码</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">provider</span><span class="token operator">-></span><span class="token function">getRegisterCode</span><span class="token punctuation">(</span>
                        <span class="token keyword type-hint">string</span> <span class="token variable">$corpName</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span> <span class="token comment">//企业名称</span>
                        <span class="token keyword type-declaration">string</span> <span class="token variable">$adminName</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span><span class="token comment">//管理员姓名</span>
                        <span class="token keyword type-declaration">string</span> <span class="token variable">$adminMobile</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span><span class="token comment">//管理员手机号</span>
                        <span class="token keyword type-declaration">string</span> <span class="token variable">$state</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token comment">//自定义的状态值</span>
                    <span class="token punctuation">)</span><span class="token punctuation">;</span> 
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h3 id="获取注册uri" tabindex="-1"><a class="header-anchor" href="#获取注册uri" aria-hidden="true">#</a> 获取注册Uri</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">provider</span><span class="token operator">-></span><span class="token function">getRegisterUri</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$registerCode</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">//$registerCode 注册码</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="查询注册状态" tabindex="-1"><a class="header-anchor" href="#查询注册状态" aria-hidden="true">#</a> 查询注册状态</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">provider</span><span class="token operator">-></span><span class="token function">getRegisterInfo</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$registerCode</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">//$registerCode 注册码</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="设置授权应用可见范围" tabindex="-1"><a class="header-anchor" href="#设置授权应用可见范围" aria-hidden="true">#</a> 设置授权应用可见范围</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">provider</span><span class="token operator">-></span><span class="token function">setAgentScope</span><span class="token punctuation">(</span>
                        <span class="token keyword type-hint">string</span> <span class="token variable">$accessToken</span><span class="token punctuation">,</span> <span class="token comment">//查询注册状态接口返回的access_token</span>
                        <span class="token keyword type-declaration">string</span> <span class="token variable">$agentId</span><span class="token punctuation">,</span> <span class="token comment">//	授权方应用id</span>
                        <span class="token keyword type-declaration">array</span> <span class="token variable">$allowUser</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token comment">//应用可见范围（成员）若未填该字段，则清空可见范围中成员列表</span>
                        <span class="token keyword type-declaration">array</span> <span class="token variable">$allowParty</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token comment">//	应用可见范围（部门）若未填该字段，则清空可见范围中部门列表</span>
                        <span class="token keyword type-declaration">array</span> <span class="token variable">$allowTag</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span> <span class="token comment">//应用可见范围（标签）若未填该字段，则清空可见范围中标签列表</span>
                    <span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><h3 id="设置通讯录同步完成" tabindex="-1"><a class="header-anchor" href="#设置通讯录同步完成" aria-hidden="true">#</a> 设置通讯录同步完成</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">provider</span><span class="token operator">-></span><span class="token function">contactSyncSuccess</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$accessToken</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">//$accessToken //查询注册状态接口返回的access_token</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="通讯录单个搜索" tabindex="-1"><a class="header-anchor" href="#通讯录单个搜索" aria-hidden="true">#</a> 通讯录单个搜索</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">provider</span><span class="token operator">-></span><span class="token function">searchContact</span><span class="token punctuation">(</span>
                         <span class="token keyword type-hint">string</span> <span class="token variable">$corpId</span><span class="token punctuation">,</span> <span class="token comment">//查询的企业corpid</span>
                         <span class="token keyword type-declaration">string</span> <span class="token variable">$queryWord</span><span class="token punctuation">,</span> <span class="token comment">//搜索关键词。当查询用户时应为用户名称、名称拼音或者英文名；当查询部门时应为部门名称或者部门名称拼音</span>
                         <span class="token keyword type-declaration">string</span> <span class="token variable">$agentId</span><span class="token punctuation">,</span> <span class="token comment">//授权方应用id</span>
                         <span class="token keyword type-declaration">int</span> <span class="token variable">$offset</span> <span class="token operator">=</span> <span class="token number">0</span><span class="token punctuation">,</span> <span class="token comment">//查询的偏移量，每次调用的offset在上一次offset基础上加上limit</span>
                         <span class="token keyword type-declaration">int</span> <span class="token variable">$limit</span> <span class="token operator">=</span> <span class="token number">50</span><span class="token punctuation">,</span> <span class="token comment">//查询返回的最大数量，默认为50，最多为200，查询返回的数量可能小于limit指定的值</span>
                         <span class="token keyword type-declaration">int</span> <span class="token variable">$queryType</span> <span class="token operator">=</span> <span class="token number">0</span><span class="token punctuation">,</span> <span class="token comment">//查询类型 1：查询用户，返回用户userid列表 2：查询部门，返回部门id列表。 不填该字段或者填0代表同时查询部门跟用户</span>
                         <span class="token variable">$fullMatchField</span> <span class="token operator">=</span> <span class="token constant">null</span> <span class="token comment">//如果需要精确匹配用户名称或者部门名称或者英文名，不填则默认为模糊匹配；1：匹配用户名称或者部门名称 2：匹配用户英文名</span>
                     <span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br></div></div>